import React from 'react'

import style from './collections.module.css'

import Collection from './collection'

const Collections = ({
    triangleNodes,
    dreamNodes,
    bitsNodes,
}) =>
    <div className={style.container}>
        <Collection name='triangles' images={triangleNodes.map(n => n.node)} />
        <Collection name='dream within a dream' images={dreamNodes.map(n => n.node)} />
        <Collection name='triangles' images={bitsNodes.map(n => n.node)} />
    </div>

export default Collections


